import React, { useState, useEffect } from 'react';
import Layout from '../../components/Layout';
import {
  Box,
  Typography,
  Button,
  IconButton,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions,
  TextField,
} from '@mui/material';
import VisibilityIcon from '@mui/icons-material/Visibility';
import LinkIcon from '@mui/icons-material/Link';
import { SnackbarAlert } from '../../components/placeholder/SnackbarAlert';
import { CodeEditor } from '../../components/placeholder/CodeEditor';
import { AppOutput } from '../../components/placeholder/AppOutput';
import { AIChat } from '../../components/placeholder/AIChat';
import { PromptSection } from '../../components/placeholder/PromptSection';
import { basicHTMLApp } from '../../helpers/git';
import LoginModal from '../../components/LoginModal';
import Parse from '../../vendor/Parse';

const AppPage = ({ location, placeholder }) => {
  const [state, setState] = useState({
    prompt: '',
    enhancedPrompt: '',
    files: {
      'index.html': basicHTMLApp, // Default file
    },
    snackbarOpen: false,
    snackbarMessage: '',
    snackbarSeverity: 'info',
    systemPrompt: 'as a frontend developer, create a web application for the above in a single html file',
    showCodeEditor: true,
    showAppOutput: true,
    showAI: true,
    appName: 'MyApp',
    loginModalOpen: false,
    user: null,
    isAppDeployed: false,
    deployModalOpen: false,
    isDeploying: false,
    deployStatusMessage: '',
    isPromoting: false, // New state for promotion loading
    promoteModalOpen: false, // New state for promote modal
    src: placeholder, // Pre-populate src with the placeholder (app ID)
    dest: '', // Destination app ID
    token: '', // Token for promotion
  });

  const {
    prompt,
    enhancedPrompt,
    files,
    snackbarOpen,
    snackbarMessage,
    snackbarSeverity,
    systemPrompt,
    showCodeEditor,
    showAppOutput,
    showAI,
    appName,
    loginModalOpen,
    user,
    isAppDeployed,
    deployModalOpen,
    isDeploying,
    deployStatusMessage,
    isPromoting,
    promoteModalOpen,
    src,
    dest,
    token,
  } = state;

  // Check if the query string contains 'p'
  const queryParams = new URLSearchParams(location.search);
  const showPromoteButton = queryParams.has('p') && isAppDeployed; // Only show if app is deployed

  useEffect(() => {
    const fetchAppData = async () => {
      try {
        const result = await Parse.Cloud.run('getApp', { id: placeholder });
        if (result?.files && Object.keys(result.files).length > 0) {
          setState((prev) => ({ ...prev, files: result.files, isAppDeployed: true }));
        } else {
          setState((prev) => ({ ...prev, isAppDeployed: false }));
        }
      } catch (error) {
        console.error('Error fetching app data:', error);
        setState((prev) => ({ ...prev, isAppDeployed: false }));
      }
    };

    fetchAppData();
  }, [placeholder]);

  useEffect(() => {
    const combinedCode = Object.values(files).join('\n\n');
    setState((prev) => ({ ...prev, enhancedPrompt: `${combinedCode}\n\n${prompt}\n\n${systemPrompt}` }));
  }, [prompt, files, systemPrompt]);

  useEffect(() => {
    const currentUser = Parse.User.current();
    if (currentUser) setState((prev) => ({ ...prev, user: currentUser }));
  }, []);

  const handleCopyPrompt = () => {
    navigator.clipboard.writeText(enhancedPrompt).then(() => {
      setState((prev) => ({ ...prev, snackbarMessage: 'Prompt copied to clipboard!', snackbarSeverity: 'success', snackbarOpen: true }));
    });
  };

  const handlePromptChange = (e) => {
    setState((prev) => ({ ...prev, prompt: e.target.value }));
  };

  const handleDeploy = async () => {
    try {
      const result = await Parse.Cloud.run('deployApp', { appname: appName, files, placeholder });
      setState((prev) => ({
        ...prev,
        snackbarMessage: 'App deployed successfully!',
        snackbarSeverity: 'success',
        snackbarOpen: true,
        isAppDeployed: true,
        deployStatusMessage: 'A link will appear beside the app name',
      }));

      setTimeout(() => {
        setState((prev) => ({ ...prev, deployStatusMessage: '' }));
      }, 3000);

      console.log('Deployment result:', result);
    } catch (error) {
      setState((prev) => ({
        ...prev,
        snackbarMessage: 'Failed to deploy app. Please try again.',
        snackbarSeverity: 'error',
        snackbarOpen: true,
      }));
      console.error('Deployment error:', error);
    }
  };

  const handleLoginSuccess = () => {
    const currentUser = Parse.User.current();
    setState((prev) => ({ ...prev, user: currentUser }));
  };

  const handleDeployClick = () => {
    setState((prev) => ({ ...prev, deployModalOpen: true }));
  };

  const handleDeployConfirm = () => {
    setState((prev) => ({ ...prev, deployModalOpen: false, isDeploying: true }));
    handleDeploy();
  };

  const handleFilesChange = (newFiles) => {
    setState((prev) => ({ ...prev, files: newFiles }));
  };

  const handlePromoteClick = () => {
    setState((prev) => ({ ...prev, promoteModalOpen: true }));
  };

  const handlePromoteConfirm = async () => {
    setState((prev) => ({ ...prev, isPromoting: true }));

    try {
      // Call the copyApp Parse function with src, dest, and token
      const result = await Parse.Cloud.run('copyApp', { src, dest, token });

      // Handle the result
      setState((prev) => ({
        ...prev,
        snackbarMessage: 'App promoted successfully!',
        snackbarSeverity: 'success',
        snackbarOpen: true,
        isPromoting: false,
        promoteModalOpen: false, // Close the modal after promotion
      }));

      console.log('Promotion result:', result);
    } catch (error) {
      setState((prev) => ({
        ...prev,
        snackbarMessage: 'Failed to promote app. Please try again.',
        snackbarSeverity: 'error',
        snackbarOpen: true,
        isPromoting: false,
      }));
      console.error('Promotion error:', error);
    }
  };

  const handlePromoteModalClose = () => {
    setState((prev) => ({ ...prev, promoteModalOpen: false }));
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setState((prev) => ({ ...prev, [name]: value }));
  };

  return (
    <Layout
      deployButton={<Button variant="contained" onClick={handleDeployClick}>Deploy</Button>}
      promoteButton={
        showPromoteButton && (
          <Button
            variant="contained"
            color="secondary"
            onClick={handlePromoteClick}
            disabled={isPromoting} // Disable button while promoting
          >
            Promote
          </Button>
        )
      }
      appName={appName}
    >
      <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center', minHeight: '80vh', textAlign: 'center' }}>
        <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
          <Typography variant="h3" component="h1" gutterBottom>{appName}</Typography>
          {isAppDeployed && (
            <IconButton color="primary" aria-label="app deployed" component="a" href={`https://build.appseek.ai/${placeholder}`} target="_blank">
              <LinkIcon />
            </IconButton>
          )}
        </Box>

        {deployStatusMessage && <Typography variant="body1" color="textSecondary">{deployStatusMessage}</Typography>}

        <Box sx={{ display: 'flex', gap: 2, mb: 2 }}>
          {!showCodeEditor && <Button variant="outlined" onClick={() => setState((prev) => ({ ...prev, showCodeEditor: true }))} startIcon={<VisibilityIcon />}>Show Code Editor</Button>}
          {!showAppOutput && <Button variant="outlined" onClick={() => setState((prev) => ({ ...prev, showAppOutput: true }))} startIcon={<VisibilityIcon />}>Show App Output</Button>}
          {!showAI && <Button variant="outlined" onClick={() => setState((prev) => ({ ...prev, showAI: true }))} startIcon={<VisibilityIcon />}>Show AI</Button>}
        </Box>

        <Box sx={{ display: 'flex', flexDirection: { xs: 'column', md: 'row' }, width: '100%', gap: 2, mt: 2 }}>
          {showCodeEditor && (
            <CodeEditor
              files={files}
              setFiles={handleFilesChange}
              showCodeEditor={showCodeEditor}
              setShowCodeEditor={(value) => setState((prev) => ({ ...prev, showCodeEditor: value }))}
            />
          )}
          {showAppOutput && <AppOutput files={files} showAppOutput={showAppOutput} setShowAppOutput={(value) => setState((prev) => ({ ...prev, showAppOutput: value }))} />}
          {showAI && <AIChat showAI={showAI} setShowAI={(value) => setState((prev) => ({ ...prev, showAI: value }))} />}
        </Box>

        <PromptSection
          prompt={prompt}
          setPrompt={(value) => setState((prev) => ({ ...prev, prompt: value }))}
          enhancedPrompt={enhancedPrompt}
          handleCopyPrompt={handleCopyPrompt}
          systemPrompt={systemPrompt}
          setSystemPrompt={(value) => setState((prev) => ({ ...prev, systemPrompt: value }))}
        />
      </Box>

      <LoginModal open={loginModalOpen} onClose={() => setState((prev) => ({ ...prev, loginModalOpen: false }))} onLoginSuccess={handleLoginSuccess} />

      <Dialog open={deployModalOpen} onClose={() => setState((prev) => ({ ...prev, deployModalOpen: false }))}>
        <DialogTitle>Bookmark This App</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Before deploying, we recommend bookmarking this app to avoid losing it. Press <strong>Ctrl+D</strong> (Windows/Linux) or <strong>Cmd+D</strong> (Mac) to bookmark this page. Once Deployed, there will be a link beside the app name.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setState((prev) => ({ ...prev, deployModalOpen: false }))}>Cancel</Button>
          <Button onClick={handleDeployConfirm} color="primary" variant="contained">Deploy</Button>
        </DialogActions>
      </Dialog>

      {/* Promote Modal */}
      <Dialog open={promoteModalOpen} onClose={handlePromoteModalClose}>
        <DialogTitle>Promote App</DialogTitle>
        <DialogContent>
          <TextField
            fullWidth
            label="Source App ID"
            name="src"
            value={`${src}/index.html`}
            onChange={handleInputChange}
            margin="normal"
            disabled // Source is pre-populated and disabled
          />
          <TextField
            fullWidth
            label="Destination App ID"
            name="dest"
            value={`${dest}`}
            onChange={handleInputChange}
            margin="normal"
          />
          <TextField
            fullWidth
            label="Token"
            name="token"
            value={token}
            onChange={handleInputChange}
            margin="normal"
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handlePromoteModalClose}>Cancel</Button>
          <Button onClick={handlePromoteConfirm} color="primary" variant="contained" disabled={isPromoting}>
            {isPromoting ? 'Promoting...' : 'Promote'}
          </Button>
        </DialogActions>
      </Dialog>

      <SnackbarAlert open={snackbarOpen} message={snackbarMessage} severity={snackbarSeverity} onClose={() => setState((prev) => ({ ...prev, snackbarOpen: false }))} />
    </Layout>
  );
};

export default AppPage;