import React, { useState, useEffect } from 'react';
import Editor from '@monaco-editor/react';
import { Box, Typography, IconButton, FormControl, Select, MenuItem } from '@mui/material';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';

export const CodeEditor = ({ files, setFiles, showCodeEditor, setShowCodeEditor }) => {
  // State to track the currently active file
  const [activeFile, setActiveFile] = useState(Object.keys(files)[0] || '');

  // Sync activeFile with files
  useEffect(() => {
    const fileKeys = Object.keys(files);
    if (fileKeys.length > 0 && !fileKeys.includes(activeFile)) {
      // If activeFile is no longer valid, reset it to the first file
      setActiveFile(fileKeys[0]);
    } else if (fileKeys.length === 0) {
      // If files is empty, reset activeFile
      setActiveFile('');
    }
  }, [files, activeFile]);

  // Function to handle file selection change
  const handleFileChange = (event) => {

    setActiveFile(event.target.value);
  };

  // Function to handle code changes in the editor
  const handleCodeChange = (value) => {
    setFiles({
      ...files,
      [activeFile]: value,
    });
  };

  // Function to detect the language based on the file extension
  const getLanguageFromFileName = (fileName) => {
    const extension = fileName.split('.').pop();
    switch (extension) {
      case 'html':
        return 'html';
      case 'css':
        return 'css';
      case 'js':
        return 'javascript';
      default:
        return 'plaintext';
    }
  };

  return (
    <Box sx={{ flex: 1, border: '1px solid #ccc', borderRadius: '4px', overflow: 'hidden', height: '600px' }}>

      
      {/* Header Section */}
      <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', borderBottom: '1px solid #ccc', height: '48px', position: 'relative' }}>
        <Typography variant="h6" component="h2" sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center', flex: 1, margin: 0 }}>
          Code
        </Typography>

        {/* File Selection Dropdown */}
        <FormControl variant="outlined" sx={{ minWidth: 100, height: '36px', position: 'absolute', left: 16 }}>
          <Select
            value={activeFile}
            onChange={handleFileChange}
            sx={{ 
              height: '100%', 
              display: 'flex', 
              alignItems: 'center', 
              fontSize: '0.875rem', // Smaller font size
              padding: '6px 32px 6px 12px', // Smaller padding
            }}
          >
            {Object.keys(files).map((fileName) => (
              <MenuItem 
                key={fileName} 
                value={fileName}
                sx={{ fontSize: '0.875rem' }} // Smaller font size for menu items
              >
                {fileName}
              </MenuItem>
            ))}
          </Select>
        </FormControl>

        {/* Close Button */}
        <IconButton
          onClick={() => setShowCodeEditor(false)}
          sx={{ color: 'primary.main', position: 'absolute', right: 0 }}
        >
          <VisibilityOffIcon />
        </IconButton>
      </Box>

      {/* Editor Section */}
      <Box sx={{ height: 'calc(100% - 48px)', overflowY: 'auto' }}>
        <Editor
          height="100%"
          language={getLanguageFromFileName(activeFile)} // Dynamically set language
          value={files[activeFile] || ''} // Fallback for empty files
          onChange={handleCodeChange}
          options={{
            fontSize: 14,
            scrollBeyondLastLine: false,
            minimap: { enabled: false }, // Disable minimap
            wordWrap: 'on', // Enable word wrap
            automaticLayout: true, // Automatically adjust layout
          }}
        />
      </Box>
    </Box>
  );
};