import React, { useRef, useState, useEffect } from 'react';
import { Box, Typography, IconButton } from '@mui/material';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';

export const AppOutput = ({ files, code, showAppOutput, setShowAppOutput }) => {
  const iframeRef = useRef(null); // Ref for the iframe
  const [iframeHeight, setIframeHeight] = useState('100%'); // State to manage iframe height

  // Extract the content of index.html from the files or code object
  const indexHtmlContent = files?.['index.html'] || code?.files?.['index.html'] || '';

  // Function to resize the iframe based on its content
  const resizeIframe = () => {
    if (iframeRef.current) {
      const iframeDocument = iframeRef.current.contentDocument || iframeRef.current.contentWindow.document;
      const body = iframeDocument.body;
      const html = iframeDocument.documentElement;

      // Calculate the height of the content
      const height = Math.max(
        body.scrollHeight,
        body.offsetHeight,
        html.clientHeight,
        html.scrollHeight,
        html.offsetHeight
      );

      // Set the iframe height to match the content height
      setIframeHeight(`${height}px`);
    }
  };

  // Resize the iframe whenever the content changes
  useEffect(() => {
    if (iframeRef.current) {
      iframeRef.current.onload = resizeIframe;
    }
  }, [indexHtmlContent]);

  return (
    <Box
      sx={{
        flex: 1,
        border: '1px solid #ccc',
        borderRadius: '4px',
        overflow: 'hidden',
        height: '600px', // Fixed height for the parent container
        display: 'flex',
        flexDirection: 'column',
      }}
    >
      {/* Header Section */}
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          borderBottom: '1px solid #ccc',
          height: '48px',
          position: 'relative',
          flexShrink: 0, // Prevent header from shrinking
          backgroundColor: '#f5f5f5', // Optional: Add background color
        }}
      >
        <Typography
          variant="h6"
          component="h2"
          sx={{
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            flex: 1,
            margin: 0,
            fontWeight: 'bold', // Optional: Add font weight
          }}
        >
          App Output
        </Typography>
        <IconButton
          onClick={() => setShowAppOutput(false)}
          sx={{ color: 'primary.main', position: 'absolute', right: 0 }}
        >
          <VisibilityOffIcon />
        </IconButton>
      </Box>

      {/* Scrollable Content Section */}
      <Box
        sx={{
          flex: 1,
          overflowY: 'auto', // Enable vertical scrolling for the parent container
          height: 'calc(100% - 48px)', // Subtract header height
        }}
      >
        <iframe
          ref={iframeRef} // Attach the ref to the iframe
          srcDoc={indexHtmlContent} // Use the content of index.html
          title="Rendered Output"
          width="100%"
          height={iframeHeight} // Use dynamic height
          style={{ border: 'none' }} // Remove iframe border
          onLoad={resizeIframe} // Resize iframe when content loads
        />
      </Box>
    </Box>
  );
};